body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-image: url(/static/media/janko-ferlic-174927-unsplash.b4eeb287.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #2E364D;
    color: #E9E8EC;
    font-family: 'PT Mono', monospace;
    font-family: 'PT Sans', sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-size: medium;
    font-weight: bold;
}

.open>.dropdown-menu {
    display: contents;
}

.form-control {
    display: inline;
    width: 40%;
    border: 1px solid #D49874;
    color: #D49874;
}

.panel-default>.panel-heading {
    color: #E9E8EC;
    background-color: #2E364D;
    border-color: #2E364D;
}

.panel-default {
    border-color: #2E364D;
    background: #4D526F;
    opacity: 0.9;
    width: 80%;
    margin-left: 10%;
}

.btn-default {
    color: #D49874;
    background-color: #E9E8EC;
    border-color: #D49874;
}

.btn-primary {
    color: #E9E8EC;
    background-color: #D49874;
    border-color: #D49874;
}

.btn-primary-dark {
    color: #E9E8EC;
    background-color: #2E364D;
    border-color: #2E364D;
}

.btn-toolbar .btn,
.btn-toolbar .btn-group,
.btn-toolbar .input-group {
    float: right;
}

.btn-group-vertical>.btn,
.btn-group>.btn {
    position: relative;
    float: inherit;
}

.btn-group>.btn:first-child:not(:last-child):not(.dropdown-toggle) {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.btn-group-xs>.btn,
.btn-xs {
    margin-left: 10px;
}

.dropdown-menu {
    min-width: 400px;
}

.App {
    text-align: center;
    line-height: 2.5;
}

.Booklist {
    list-style: none;
    /* may want to change this later: */
    margin: 10px;
}

.Search {
    min-height: 150px;
    margin-top: 100px;
}

.TrashBtn {
    margin-left: 20px;
}

.LoginPanel {
    background-color: #D49874;
    opacity: 0.9;
    border-color: #D49874;
    padding-top: 30px;
}


/* .profile {
    text-align: left;
    left: 10px;
} */

.loginbutton {
    position: fixed;
    top: 10px;
    right: 10px;
}

.logoutbutton {
    position: fixed;
    top: 10px;
    right: 10px;
}

.container {
    max-width: 800px;
    /* max-height: 100px; */
    margin-top: 10px;
}
body {
    min-height: 100%;
}

.RegisterForm {
    max-width: 400px;
    /* max-height: 100px; */
    margin-top: 10px;
    align-self: center;
}

.signup-screen {
    padding: 20px;
    padding-bottom: 40px;
    border-radius: 5px;
    background-color: #2E364D;
    opacity: 0.9;
    box-shadow: 0 1px 6px rgba(0, 0, 0, .3);
    color: #E9E8EC;
}

.center {
    margin: auto;
    width: 50%;
    padding: 20px;
    margin-top: 100px;
}
